.scroll-button {
  display: none;
  border: none;
  margin: 0;
  padding: 0;
  &:hover, &:focus, &:visited, &:active {
    outline: none;
    border: none;
  }
}

@media (min-width: 1200px) {
 .scroll-button {
   display: none;
   position: fixed;
   right: 0;
   top: calc(20% + 10rem);
   transform: translateY(-50%);
   background-color: $white;
   z-index: 100;
   box-sizing: border-box;
   cursor: pointer;
   padding: .4em .15em 3em;
   border-radius: .3em;
   clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%, 0 75%);

   span {
     display: block;
     color: $active;
     font-size: px-to-rem(14px);
     line-height: px-to-rem(16px);
     font-weight: bold;
     text-align: left;
     transform: rotate(-90deg);
     margin-top: 1em;
   }
 }

}