.section__shop {
  background: url("../img/svg/basket.svg") no-repeat center;
  background-size: contain;
  position: relative;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: .9;
  }

  .title {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(37px);
    font-weight: bold;
    text-align: center;
    margin-bottom: .5em;
    color: $active;
  }

  .shop__list, .trading-platforms__list {
    margin: 0;
    margin-bottom: 2.5em;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    color: $active;


    .item {
      list-style: none;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      width: 90%;
      font-size: px-to-rem(24px);
      line-height: px-to-rem(28px);
      font-weight: bold;
      background-color: $white;
      border: 1px solid $active;
      margin-bottom: 1em;
    }
  }

  .subtitle {
    font-size: px-to-rem(24px);
    line-height: px-to-rem(28px);
    font-weight: bold;
    text-align: center;
    margin-bottom: 1.5em;
    color: $active;
  }

  .trading-platforms__list{
    align-items: center;
    margin-bottom: 4em;
  }

  .trading-item {
    list-style: none;
    display: flex;
    font-size: px-to-rem(32px);
    padding-bottom: 1em;
  }

  .trading_btn {
    a {
      &:hover, &:focus{
        background-color: inherit;
      }
    }
  }
  .social__networks {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;

    li {
      margin-right: .4em;
    }

    li:last-child {
      margin-right: 0;
    }
  }

}

@media (min-width: 480px) {
.section__shop {

  .title {
    font-size: px-to-rem(48px);
    line-height: px-to-rem(56px);
  }

  .shop__list, .trading-platforms__lis {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .item {
      width: 80%;

      .item_icon {
        display: block;
        height: px-to-rem(70px);
      }
    }
  }


  .subtitle {
    font-size: px-to-rem(36px);
    line-height: px-to-rem(42px);
  }

  .item_text {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(38px);
    padding: 0 1em;
  }
}

}
@media (min-width: 640px) {
 .section__shop {
   .shop__list, .trading-platforms__list {
     flex-direction: row;
     flex-wrap: wrap;
     justify-content: center;

     .item {
       flex-basis: 18em;
       font-size: px-to-rem(28px);
       line-height: px-to-rem(32px);
       margin: 1em;
     }
   }

   .trading-item{
     padding: 0 1em;

     .trading_icon {
       display: block;
       height: px-to-rem(70px);
       width: auto;
     }
   }

   .subtitle {
     font-size: px-to-rem(38px);
     line-height: px-to-rem(48px);
   }


   .social__networks {
     li {
       padding: .5em;
     }

   }

 }
}

@media (min-width: 1024px){
  .section__shop {
    background: url("../img/svg/logo jeep.svg") center no-repeat;
    background-size: contain;

    .title {
      font-size: px-to-rem(52px);
      line-height: px-to-rem(60px);
    }

    .subtitle {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(50px);
    }
  }


}

@media (min-width: 1200px) {
  .section__shop {
    .shop__list {
      margin-left: 2em;
    }
    .social__networks {
      flex-direction: column;
      position: absolute;
      top: 20%;
      left: 1em;

      li {
        margin-bottom: .4em;
      }

      li:last-child {
        margin-bottom: 0;
      }
    }
  }

}

@media  (min-width: 1500px) {
  .section__shop {
    .title {
      font-size: px-to-rem(72px);
      line-height: px-to-rem(80px);
    }

    .subtitle {
      font-size: px-to-rem(48px);
      line-height: px-to-rem(52px);
    }
  }
}



