.section__contacts {
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(37px);
    font-weight: bold;
    text-align: center;
    margin-bottom: 1.5em;
    color: $active;
  }

  .contacts__info {
    max-width: 37.5em;
    background-color: $active;
    border-radius: 1.5em;
    padding: 1em;
    font-size: px-to-rem(18px);
    line-height: px-to-rem(21px);

    p{
      padding: 0;
      margin: .9em 0;

      &:last-child{
        margin: 0;
      }
    }
    .contacts__time-work {
      color: $background;
      font-weight: bold;
      text-align: center;
      margin-top: 0;
    }

    .contacts__phone, .contacts__mail, .contacts__address {
      span {
        display: block;
        color: $black;
        font-weight: bold;
        padding-left: 1.5rem;
        margin-bottom: .3em;
        background: url("../img/svg/tel.svg") left center no-repeat;
      }

      a {
        display: inline-block;
        padding-left: 1rem;
        transition: all .5s ease 0s;

        &:hover {
          background-color: inherit;
          color: $light-active;
          transition: all .5s ease 0s;
        }
      }
    }

    .contacts__mail {
      span {
        background: url("../img/svg/mail.svg") left center no-repeat;
      }
    }
    .contacts__address {
      span {
        background: url("../img/svg/geolocation.svg") left center no-repeat;
        background-size: contain;
      }
    }
  }

  .contacts__map {
    background: url("../img/svg/map-big.svg") center no-repeat;
    background-size: cover;
    height: 400px;
    width: 90%;
    position: relative;
    z-index: 1;

    &::before {
      background: radial-gradient(circle, transparent 10%, $background 50% );
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
    }
  }

  .contacts__developer {
    text-align: center;
  }

}

@media (min-width: 720px){
  .section__contacts {
    .contacts__map {
      margin: 3em 0;
      height: 35em;
      width: 90%;

    }
  }
}

@media (min-width: 1024px) {
  .section__contacts {

    .title {
      display: block;
      width: 100%;
      font-size: px-to-rem(56px);
      line-height: px-to-rem(64px);
      margin-bottom: 1em;
    }

    .contacts__info {
      font-size: px-to-rem(24px);
      line-height: px-to-rem(28px);
    }

    .contacts__map {

      height: 37.5em;
      width: 90%;
    }
  }
}

@media (min-width: 1200px) {
  .section__contacts{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;


    .title {
      font-size: px-to-rem(64px);
      line-height: px-to-rem(72px);
      margin-bottom: 0;
    }

    .contacts__info {
      width: 35%;
    }

    .contacts__map {
      width: 40%;
      margin: 0;
    }

    .contacts__developer {
      margin-top: 4em;
      width: 100%;
    }
  }

}

@media (min-width: 1500px) {
  .section__contacts {
    margin-bottom: 18em;

    .title {
      font-size: px-to-rem(72px);
      line-height: px-to-rem(84px);
    }

  }
  footer {
    display: none;
  }
}