.footer {
  display: none;
}

@media (min-width: 1500px ) {
  .footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 1em 4em 0;
    border-top: 3px solid $white;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(12px);
    background-color: $background;
    z-index: 20;

    a:hover, :focus {
      background-color: inherit;
    }

    .footer__address{
      width: 25%;

      .bold {
        font-weight: bold;
      }

    }

    .footer__map_debugger {
      max-width: 30%;
      margin-left: 4em;
      margin-right: 1em;
      .footer_map {
        .map__picture{
          max-width: 100%;
          height: 100px;
        }
      }
      .debugger {
        text-align: center;
      }

    }

    .footer__menu {
      width: 20%;
    }


    .footer__menu_link {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      padding: 0;
      margin: 0;

      .footer__menu_item {
        list-style-type: none;
        padding-right: 4em;
        padding-bottom: 0em;

        .footer__menu_icon {
          height: 3em;
          width: auto;

          &:first-child {
            margin-bottom: 2em;
          }
        }
      }
    }

    .developer {
      padding: 14px 0;
      display: block;
      width: 10%;

      a {
        display: inline-block;
        transition: all .5s ease 0s;
        p {
          display: inline-block;
          margin: 0;
          padding: 0;
          text-transform: uppercase;
          font-size: px-to-rem(16px);
        }


        &:hover {
          background-color: inherit;
          color: $light-active;
          transition: all .5s ease 0s;
        }
      }
    }


  }
  .fixed__bottom {
    position: fixed;
    bottom: 0px;
    width: 100%;
    transition: all 0.4s ease;
  }
}

@media (min-width: 1620px) {
  .footer {
    font-size: px-to-rem(16px);
    line-height: px-to-rem(14px);
  }

}
@media (min-width: 1850px) {
  .footer {
    .footer__map_debugger {
      .footer_map {
        .map__picture{
          height: 100px;
        }
      }

    }

    .footer__menu {
      .footer__menu_link {
        .footer__menu_item {
          padding-right: 5em;
          padding-bottom: 0em;
        }
      }

    }
  }

}




