.top__menu_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  max-height: 90px;
  z-index: 25;
  padding: 0 15px;


  .top__menu_language {
    display: flex;
    align-items: center;
    justify-content: start;
  }

  .dropbtn {
    padding: 5px;
    border: none;
    cursor: pointer;
    background-color: inherit;
    color: inherit;
    outline: none;
    font-style: italic;


    &:hover, &:focus {
      background-color: $active;
    }
  }

  .dropdown {
    position: relative;
    display: inline-block;



    .dropdown__content {
      display: none;
      position: absolute;
      background-color: $light-active;
      overflow: auto;
      z-index: 15;


      a {
        display: block;
        padding: 0.5rem 1.3rem;

      }

    }

    .show {
      display: block;

    }

  }

  .top__menu_days, .top__menu_phone {
    display: none;
  }

}

@media (min-width: 640px ) {
  .top__menu_wrap {
    justify-content: space-between;

    .top__menu_days, .top__menu_phone {
      display: block;
    }
  }

}