.hamburger-menu {
  position: sticky;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease;

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: $background;
    opacity: 0;
    z-index: -1;
  }

  .menu-burger__list {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    height: 100%;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    transition-property: transform;
    overflow-y: auto;
  }

  .menu_border {
    width: 100%;
    border-top: 3px solid $white;
    border-bottom: 3px solid $white;
    padding-top: 4px;
    padding-bottom: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 1.7em;
  }

  .btn {
    background: none;
    border: none;
    padding: 0 .5em;
  }

  .item_icon {
    display: block;
    width: 3em;
    height: auto;
  }

  .menu-burger_geo {
    .item_icon {
      width: 2.3em;
    }
  }

  .menu-burger__text {
    display: block;
    background: $white;
    width: 19rem;
    height: 2.38em;
    clip-path: polygon(0 0, 100% 0%, 75% 100%, 0 100%);
    font-size: px-to-rem(24px);
    line-height: px-to-rem(28px);
    font-weight: bold;
    font-style: italic;
    color: $background;
    position: relative;

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 1.7em;
      left: 0;
      width: 60%;
      height: 3px;
      background-color: $background;
    }
  }

  .menu-burger__close {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    background: url("../img/svg/close.svg") left center no-repeat;
    cursor: pointer;
  }

  .item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: px-to-rem(24px);
    line-height: px-to-rem(32px);
    border: 1px solid $active;
    margin: 1em;

    .item_icon {
      padding: 0 .5em;
    }
  }
}

.menu-burger--active {
  z-index: 200;
  &::after {
      opacity: 1;
  }

  .menu-burger__list {
      transform: translateX(0);
      transition-property: transform;
      transition: all 0.5s ease;
    }
  }