.header {
  background: url("../img/svg/logo jeep.svg") center no-repeat;
  background-size: contain;
  position: relative;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: .9;
  }

  .header__title {
    font-size: px-to-rem(38px);
    line-height: px-to-rem(44px);
    font-weight: bold;
    text-align: center;
    padding: 0 .3em;
    padding-top: 2.5em;
    margin-bottom: 1em;
    color: $active;
  }

  .header__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;

    li {
      list-style: none;
      display: block;
      font-size: px-to-rem(24px);
      line-height: px-to-rem(32px);
      border: 1px solid $active;
      margin-bottom: 1em;
    }
  }

  .header__item_sto {
    font-size: px-to-rem(48px);
    line-height: px-to-rem(56px);
  }

  .item_icon {
    height: 2em;
    padding-right: 1em;
  }

  .item_text {
    display: block;
    margin: 0 auto;
    text-align: center;
  }
}

@media (min-width: 640px) {
.header{
  .header__title {
    font-size: px-to-rem(48px);
    line-height: px-to-rem(56px);
  }

  .header__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;

    li {
      flex-basis: 18em;
      font-size: px-to-rem(28px);
      line-height: px-to-rem(32px);
    }
  }

  .item_icon {
    margin-right: 1em;
    margin-left: 1em;
  }
}
}

@media (min-width: 1024px) {
  .header {
    .header__title {
      padding-top: 2em;
    }

    .header__list {
      li {
        flex-basis: 15em;
        font-size: px-to-rem(32px);
        line-height: px-to-rem(42px);

       &:not(.item_shop)  {
         line-height: px-to-rem(32px);
        }
      }
    }
  }
}


@media (min-width: 1200px) {
  .header {
    .header__title {
      font-size: px-to-rem(64px);
      line-height: px-to-rem(72px);
    }
  }
}

@media (min-width: 1280px) {
  .header {
    .header__list {
      li {
        margin: 1em;
      }
    }

  }
}

@media (min-width: 1500px) {
  .header {
    .header__title {
      padding-top: 1.5em;
      margin-bottom: .5em;
    }

    .header__list {
      li {
        flex-basis: 15em;
        font-size: px-to-rem(36px);
        line-height: px-to-rem(48px);

        &:not(.item_shop)  {
          line-height: px-to-rem(38px);
        }
      }
    }

  }

}

@media (min-width: 1850px) {
.header {
  .header__title {
    font-size: px-to-rem(72px);
    line-height: px-to-rem(84px);
  }

  .header__list {
    li {
      flex-basis: 15em;
      font-size: px-to-rem(52px);
      line-height: px-to-rem(62px);

      &:not(.item_shop)  {
        line-height: px-to-rem(52px);
      }
    }
  }
}
}