.section__club {
  background: url("../img/svg/club.svg") center no-repeat ;
  background-size: contain;
  font-size: px-to-rem(24px);
  line-height: px-to-rem(60px);
  position: relative;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0.9;
  }

  .club__block_title {
    .title__club_logo {
      display: none;
    }
    .title {
      font-size: px-to-rem(48px);
      font-weight: bold;
      text-align: center;
      margin-bottom: .5em;
      color: $active;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2.5em;
  }

  .section__ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 2.5em;
  }

  .section__li {
    position: relative;
    padding-left: 1em;

    &::before {
      position: absolute;
      content: "";
      width: .3em;
      height: .4em;
      background-color: $active;
      left: .2em;
      top: 1em;
      transform: skewX(-41.5deg);

    }
  }

  .social__networks {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;

    li {
      margin-right: .4em;
    }

    li:last-child {
      margin-right: 0;
    }


  }

  .club_gallery {
    margin: 0;
    padding: 0;
    width: 16rem;
    height: auto;
    margin: 0 auto 1em;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
  }

}

@media (min-width: 640px ) {
  .section__club {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }
}

@media (min-width: 1024px) {
  .section__club {
    background: url("../img/svg/logo jeep.svg") center no-repeat;
    background-size: contain;
    padding-top: 1em;


    .club__block_title {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: 0;

      .title__club_logo {
        display: block;
        position: relative;
        background: url("../img/svg/club-logo-active.svg") center no-repeat;
        background-size: contain;
        width: 5em;
        height: 5em;
      }

      .title {
        font-size: px-to-rem(56px);
        line-height: px-to-rem(64px);
        padding: 0 .5em;
      }
    }

    .content {
      flex-direction: row-reverse;
      align-items: center;

      .club_gallery {
        margin: 0 2em;
        width: 22rem;
        height: auto;
      }
    }

    .section__ul {
      font-size: px-to-rem(36px);
      line-height: px-to-rem(52px);
    }

    .section__li {
      &::before {
        top: .5em;
      }
    }

  }
}

@media (min-width: 1200px) {
  .section__club{
    .club__block_title {

      .title {
        font-size: px-to-rem(64px);
        line-height: px-to-rem(72px);
      }
    }

    .content {
      .club_gallery {
        width: 30rem;
        height: 16rem;
        margin-right: 3em;
      }
    }

    .social__networks {
      flex-direction: column;
      position: absolute;
      top: 20%;
      left: 1em;

      li {
        margin-bottom: .4em;
      }

      li:last-child {
        margin-bottom: 0;
      }
    }
  }

}

@media (min-width: 1400px) {
  .section__club{
    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }

  }
}

@media (min-width: 1500px) {
  .section__club {
    margin-bottom: 4em;
    .content {
      .club_gallery {
        width: 35rem;
        height: 19rem;
        margin-right: 6em;
      }
    }

    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }
  }
}


@media (min-width: 1700px) {
  .section__club {
    margin-bottom: 6em;
  }
}