.section__sto {
  background: url("../img/svg/sto.svg") center no-repeat ;
  background-size: contain;
  font-size: px-to-rem(24px);
  line-height: px-to-rem(60px);

  position: relative;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0.9;
  }

  .title {
    font-size: px-to-rem(48px);
    font-weight: bold;
    text-align: center;
    margin-bottom: .5em;
    color: $active;
  }

  .content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .section__img {
    display: none;
  }

  .section__ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 2.5em;

  }

  .section__li {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      width: .3em;
      height: .4em;
      background-color: $active;
      left: -.7em;
      top: 1em;
      transform: skewX(-41.5deg);
    }
  }

  .social__networks {
    list-style: none;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    justify-content: center;

    li {
      margin-right: .4em;
    }

    li:last-child {
      margin-right: 0;
    }


  }
  
}

@media (min-width: 640px ) {
 .section__sto{
   display: flex;
   flex-direction: column;
   align-items: center;

 }
}

@media (min-width: 1024px) {
  .section__sto {
    background: url("../img/svg/logo jeep.svg") center no-repeat;
    background-size: contain;

    .title {
      font-size: px-to-rem(56px);
      line-height: px-to-rem(64px);
    }

    .section__img {
      display: block;
      height: 10em;
      margin: 1em 2em 1em 0;
    }

    .section__ul {
      font-size: px-to-rem(36px);
      line-height: px-to-rem(52px);
    }

    .section__li {
      &::before {
        top: .5em;
      }
    }

  }
}

@media (min-width: 1200px) {
  .section__sto {

    .title {
      font-size: px-to-rem(64px);
      line-height: px-to-rem(72px);
    }

    .section__img {
      height: 12em;
      margin-right: 3em;
    }

    .social__networks {
      flex-direction: column;
      position: absolute;
      top: 20%;
      left: 1em;

      li {
        margin-bottom: .4em;
      }

      li:last-child {
        margin-bottom: 0;
      }
    }

  }

}

@media (min-width: 1400px) {
  .section__sto {

    .title {
      font-size: px-to-rem(72px);
      line-height: px-to-rem(84px);
    }

    .section__img {
      height: 15em;
      margin-right: 4em;
    }

    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }

  }

}

@media (min-width: 1500px) {
  .section__sto {

    .section__img {
      height: 16em;
      margin-right: 6em;
    }
    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }
  }

}